const state = {
    // 录音历史记录，二维数组，key为录音的时间，value为record对象
    history: Array(),
}

const getters = {
  getHistory:(state)=>{
    return state.history
  },
  getRecordByID:(state)=>(id) =>{
    //返回资源Id下的所有录音记录，按录音时间先后排序
    let rst = []
    for(let i=0; i<state.history.length; i++){
      if(id == state.history[i].id){
        rst.push(state.history[i])
      }
    }
    rst.sort(function(a, b){return  b.recordTime - a.recordTime});
    return rst
  },
  getRecordByTime:(state)=>(time) =>{
    return state.history[time] ? state.history[time] : null
  }
}
const mutations = {
  ADD_HISTORY(state, recordSound){
    let recordTime = recordSound['recordTime']
    state.history[recordTime] = recordSound
  }
}
export default {
  namespaced: true,
  getters,
  mutations,
  state,
}
