import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

/* Layout */
import Layout from '@/layout'
/**
 * Note: sub-menu only appear when route children.length >= 1
 * Detail see: https://panjiachen.github.io/vue-element-admin-site/guide/essentials/router-and-nav.html
 *
 * hidden: true                   if set true, item will not show in the sidebar(default is false)
 * alwaysShow: true               if set true, will always show the root menu
 *                                if not set alwaysShow, when item has more than one children route,
 *                                it will becomes nested mode, otherwise not show the root menu
 * redirect: noRedirect           if set noRedirect will no redirect in the breadcrumb
 * name:'router-name'             the name is used by <keep-alive> (must set!!!)
 * meta : {
    roles: ['admin','editor']    control the page roles (you can set multiple roles)
    title: 'title'               the name show in sidebar and breadcrumb (recommend set)
    icon: 'svg-name'             the icon show in the sidebar
    noCache: true                if set true, the page will no be cached(default is false)
    affix: true                  if set true, the tag will affix in the bottom nav bar
    breadcrumb: false            if set false, the item will hidden in breadcrumb(default is true)
    activeMenu: '/example/list'  if set path, the sidebar will highlight the path you set
  }
 */

export const constRoutes = [
  {
    path: '/',
    component: Layout,
    redirect: 'hot',
    children: [
      {
        path: 'hot',
        component: ()=>import('@/views/hot/index'),
        name: 'hot',
        meta: { title: '热门'}
      },
      {
        path: 'voice-fate',
        component: ()=>import('@/views/voice-fate/index'),
        name: 'voice-fate',
        meta: { title: '音缘'}
      },
      {
        path: 'reading',
        component: ()=>import('@/views/reading/index'),
        name: 'reading',
        meta: { title: '朗读', keepAlive: true}
      },
      {
        path: 'activity',
        component: ()=>import('@/views/activity/index'),
        name: 'activity',
        meta: { title: '活动'}
      },
      {
        path: 'my',
        component: ()=>import('@/views/my/index'),
        name: 'my',
        meta: { title: '我的'}
      },

    ]
  },
  {
    path: '/hot/reading-demo',
    component: ()=>import('@/views/hot/reading-demo'),
    name: 'reading-demo',
    meta: { title: '领读示范'}
  },
  {
    path: '/hot/ranking',
    component: ()=>import('@/views/hot/ranking'),
    name: 'hot-ranking',
    meta: { title: '排行榜'}
  },
  {
    path: '/hot/famous-detail',
    component: ()=>import('@/views/hot/famous-detail'),
    name: 'famous-detail',
    meta: { title: '朗读名家'}
  },
  {
    path: '/hot/checkin',
    component: ()=>import('@/views/hot/checkin'),
    name: 'checkin',
    meta: { title: '打卡'}
  },
  {
    path: '/reading/readnow/:id',
    component: ()=>import('@/views/reading/readnow'),
    name: 'readnow',
    meta: { title: '立刻朗读'}
  },
  {
    path: '/reading/background-music',
    component: ()=>import('@/views/reading/background-music'),
    name: 'background-music',
    meta: { title: '背景音乐'}
  },
  {
    path: '/resources/:id',
    component: ()=>import('@/views/resources/index'),
    name: 'resources',
    props: true,
    meta: { title: '素材'}
  },
  {
    path: '/resources/search',
    component: ()=>import('@/views/resources/search'),
    name: 'resources-search',
    meta: { title: '搜索素材'}
  },
  {
    path: 'player',
    component: ()=>import('@/views/player'),
    name: 'player',
    meta: { title: '播放'}
  },
  {
    path: '/my/auth/message',
    component: ()=>import('@/views/my/message'),
    name: 'my-message',
    meta: { title: '消息通知'}
  }, 
   {
    path: '/my/auth/advice',
    component: ()=>import('@/views/my/advice'),
    name: 'my-advice',
    meta: { title: '意见反馈'}
  },
  {
    path: '/my/auth/sponsor',
    component: ()=>import('@/views/my/sponsor'),
    name: 'my-sponsor',
    meta: { title: '友情赞助'}
  },
  {
    path: '/my/contact',
    component: ()=>import('@/views/my/contact'),
    name: 'my-contact',
    meta: { title: '联系方式'}
  }
]

export const otherRoutes = [
  {
    path: '/login',
    component:  ()=>import('@/views/login/index')
  },
  {
    path: '/redirect',
    component: Layout,
    hidden: true,
    children: [
      {
        path: '/redirect/:path(.*)',
        component: ()=>import('@/views/redirect/index')
      }
    ]
  },
  // 404 page must be placed at the end !!!
  { path: '*', component: ()=>import('@/views/404') }
]

const createRouter = () => new Router({
  //mode: 'history', // require service support
  scrollBehavior: () => ({ y: 0 }),
  routes: [...constRoutes,...otherRoutes],
})

const router = createRouter()

// Detail see: https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher // reset router
}

export default router
