<template>
      <van-tabbar v-model="active" route>
          <template v-for="item in tabbars">
            <van-tabbar-item  replace :to="item.path" :key="item.path">
              <span>{{item.title}}</span>
              <template #icon>
                <span :class="item.iconclass" />
              </template>
            </van-tabbar-item>
          </template>
      </van-tabbar>
</template>

<script>
import '@/styles/fonts/iconfont.css' // global css
import { Tabbar, TabbarItem } from 'vant'

export default {
  components: { [Tabbar.name]:Tabbar, [TabbarItem.name]:TabbarItem},
  data() {
    return {
      currIndex: 0,
      active: 0,
      tabbars: [
        {
          path: '/hot',
          title: '热门',
          iconclass:'iconfont icon-hot'
        },
        {
          path: '/voice-fate',
          title: '音缘', 
          iconclass:'iconfont icon-hot-spot-signal'
        },
        {
          path: '/reading',
          title: '朗读', 
          iconclass:'iconfont icon-voice'
        },
        {
          path: '/activity',
          title: '活动', 
          iconclass:'iconfont icon-activity'
        },
        {
          path: '/my',
          title: '我的', 
          iconclass:'iconfont icon-my'
        }
      ]
    };
  }
}
</script>


<style scoped>
.iconfont{
  font-size:32px
}
</style>