import { setting } from '@/settings'

const state = {
    // 播放模式， order: 顺序播放,loop: 列表循环 , single: 单曲循环, random: 随机
    playMode: "order",
    // 当前播放读经所属专辑
    album: null,
    // 播放历史
    playHistory: [],
    //播放状态
    isPlaying: false,
    //是否打开了播放页面
    pageOpened: false
}

const getters = {
  getAlbum: (state)=>{
    return state.album
  },
  getAlbumCover: (state)=>{
    if(state.album){
      if(state.album.coverImg && state.album.coverImg.length>0){
        return state.album.coverImg;
      }else{
        return setting.defaultAlbumCover
      }
    }else{
      return setting.defaultDiscCover
    }
  },
  getDiscCover: (state, getters)=>{
    return getters.getAlbumCover ? getters.getAlbumCover : setting.defaultDiscCover
  },
  getPlayMode: (state)=>{
    return state.playMode
  },
  getPlayHistory:(state)=>{
    return state.playMode
  },
  getCurrentSound:(state)=>{
    if(state.album){
      return state.album.sounds[state.album.index] ? state.album.sounds[state.album.index] : null
    }else{
      return null
    }
  },
  getIsPlaying:(state)=>{
    return state.isPlaying
  },
  isPlayerPageOpened:(state)=>{
    return state.pageOpened
  }
}
const mutations = {
  TOGGLE_PLAY (state){
    state.isPlaying = !state.isPlaying
  },
  START_PLAY(state){
    state.isPlaying = true
  },
  PAUSE_PLAY(state){
    state.isPlaying = false
  },
  END_PLAY(state){
    state.isPlaying = false
    if(state.album){
      let sound =  state.album.sounds[state.album.index] ? state.album.sounds[state.album.index] : null
      if(sound){
        sound.position = 0
      }
    }
  },
  SET_ALBUM (state, album){
    state.album = album
  },
  CHANGE_MODE (state, mode){
    state.playMode = mode
  },
  CHANGE_ORDER(state, order){
    if(state.album){
      state.album.order = order
    }
  },
  UPDATE_POSITION(state, position){
    if(state.album){
      let sound =  state.album.sounds[state.album.index] ? state.album.sounds[state.album.index] : null
      if(sound){
        sound.position = position
      }
    }
  },
  POPUP_PLAYERPAGE(state){
    state.pageOpened = true
  },
  CLOSE_PLAYERPAGE(state){
    state.pageOpened = false
  },
  SET_ALBUM_INDEX(state, newIndex){
    if(state.album){
      state.album.index = newIndex
    }
  }
}

export default {
  namespaced: true,
  getters,
  mutations,
  state,
}
