module.exports = [
  // get resource category
  {
    url: '/resource/category',
    type: 'get',
    response: config =>{
      const { id=null} = config.query
      if( null == id){
        return {
          code: 20000,
          data: category
        }
      }else{
        for(let i=0; i<category[0].subCategories.length; i++){
          let item = category[0].subCategories[i]
          if(item.id == id){
            return {
              code: 20000,
              data: item
            }
          }
        }
      }
    }
  },

  // get resource list
  {
    url: '/resource/list',
    type: 'get',
    response: config => {
      const { id=null , tag=null} = config.query

      if (id) {
        if(id == "all"){
          return {
            code: 20000,
            data: resourceListById[0]
          }
        }
        for(let i=0;i<resourceListById.length; i++){
          let item = resourceListById[i]
          if(item.id == id){
            return {
              code: 20000,
              data: item
            }
          }
        }
      }else if (tag) {
        for(let i=0;i<resourceList.length; i++){
          let item = resourceList[i]
          if(item.tag == tag){
            return {
              code: 20000,
              data: item
            }
          }
        }
      } 
      //error
      return {
        code: 20000,
        data: [],
        empty:true,
        message: "没有更多数据"
      }

    }
  },
  {
    url: '/resource/info',
    type: 'get',
    response: config => {
      const { id=null } = config.query
      if (id) {
        for(let i=0;i<resourceList.length; i++){
          let item = resourceList[i].resources
          for(let j=0; j<item.length; j++){
            if(id == item[j].id){
              return {
                code: 20000,
                data: item[j]
              }
            }
          }
        }
        return {
          code: 30000,
          data: null
        }
      }else {
        //error
        return {
          code: 20000,
          data: [],
          message: "参数错误"
        }
      } 
    }
  },
  {
    url: '/resource/correct',
    type: 'post',
    response: () =>{
      if(Math.random()*10<8){
        return {
          code: 20000,
          data: [],
          success: true,
          message: "感谢您的反馈！"
        }
      }else{
        return {
          code: 20000,
          data: [],
          success: false,
          message: "上传失败，请重试！"
        }
      }
    }
  },
  {
    url: '/resource/favorite',
    type: 'get',
    response: () =>{
      return {
        code: 20000,
        success: true,
      }
    }
  },
  {
    url: '/reading/save',
    type: 'post',
    response: () =>{
      return {
        code: 20000,
        success: true,
        message:"保存成功"
      }
    }
  }
]
